*,
*::before,
*::after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  margin: 0px;
  padding: 0px;
}

body {
  font-family: "Roboto", sans-serif;
}

.header {
  max-height: 175px;
  margin-bottom: 35px;
}

.header-lines {
  margin-bottom: 20px;
}

.header__wrapper {
  margin: 0 auto 25px;
  max-width: 1400px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__wrapper--nav {
  margin: 0 auto;
}

.header-find {
  position: relative;
  display: flex;
  align-items: center;
}

.header-find__box {
  max-width: 325px;
  width: 100%;
}

.header-find input {
  width: 100%;
  padding: 20px 0px;
  border: 0;
  border-bottom: solid 2px #000;
}

.header-find input::placeholder {
  font-size: 15px;
  color: #000;
  font-weight: 300;
}

.header-find svg {
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
}

.header-user,
.header-shop {
  font-weight: 400;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}

.header-user svg,
.header-shop svg {
  margin-right: 20px;
}

.header-user p,
.header-shop p {
  margin: 0px;
  font-size: 15px;
}

.header__nav-items {
  display: flex;
  margin: 0px;
  padding: 0px;
}

.header__nav-item {
  margin: 20px;
  list-style-type: none;
  position: relative;
}

.header__nav-item::before {
  content: "/";
  height: 100%;
  position: absolute;
  top: 0px;
  bottom: 0;
  right: -25px;
  font-size: 20px;
  font-family: "Staatliches", cursive;
}

.header__nav-item:first-child {
  margin-left: 0px;
}

.header__nav-item:last-child {
  margin-right: 0px;
}

.header__nav-item:last-child::before {
  content: unset;
}

.header__nav-item a {
  color: #000;
  font-size: 21px;
  font-family: "Staatliches", cursive;
  letter-spacing: .7px;
  text-decoration: none;
}

.header hr {
  margin: 0px;
}

/*end header*/

.container {
  margin: 0 auto;
  max-width: 1400px;
}

.home-slider__item {
  display: flex;
  justify-content: space-between;
}

.home-slider__item-left {
  margin-right: 17px;
  width: 100%;
}

.home-slider__item-right {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-left: 17px;
  font-family: "Staatliches", cursive;
  width: 100%;
  color: #fff;
  background: #ca0f0f;
}

.home-slider__item-right h2 {
  margin: 0px;
  margin-bottom: 20px;
  font-size: 49px;
  font-weight: 300;
  letter-spacing: 2.5px;
}

.home-slider__item-right p {
  margin: 0px;
  font-size: 20px;
  font-weight: 300;
}

.home-slider__box {
  margin-bottom: 40px;
}

.home-direction__box {
  margin-bottom: 55px;
}

.home-direction__items {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.home-direction__item {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0px 17px;
  padding: 35px 0px;
  width: 100%;
  font-size: 24px;
  letter-spacing: .7px;
  font-family: "Staatliches", cursive;
  border: solid 1px #000;
}

.home-direction__item:first-child {
  margin-left: 0px;
}

.home-direction__item:last-child {
  margin-right: 0px;
}

.home-subscribe__box {
  margin-bottom: 50px;
}

.home-subscribe__items {
  display: flex;
  justify-content: space-between;
}

.home-subscribe__item-left {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  margin-right: 20px;
  padding: 0px 160px 0px 180px;
  font-size: 17px;
  font-family: 'Roboto Slab', serif;
  background: #f8f8f8;
}

.home-subscribe__item-left p {
  position: relative;
  margin: 0px 0px 45px;
  line-height: 26px;
}

.home-subscribe__item-left p span {
  position: absolute;
  top: 30px;
  left: -60px;
  color: #ff6a2f;
  font-size: 105px;
}

.home-subscribe__item-left > a {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 25px;
  color: #000;
  border: solid 1px #000;
  text-decoration: none;
  font-family: "Staatliches", cursive;
  font-size: 19px;
}

.home-subscribe__item-right {
  width: 100%;
  margin-left: 20px;
}

.home-twosubscribe__box .home-subscribe__item-left a {
  color: #ff6a2f;
  text-decoration: underline;
}

.home-twosubscribe__box .home-subscribe__item-left {
  background: url(../img/home-twosubscibe--background.png);
  background-size: cover;
  position: relative;
  padding: 0px;
}

.home-twosubscribe__box .home-subscribe__item-left::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.1);
}

.home-twosubscribe__item-leftbox {
  position: relative;
  width: 450px;
  height: 450px;
  margin: auto;
  padding: 80px 60px 0px;
  background: #fff;
}

.home-twosubscribe__item-leftbox .home-twosubscribe__info {
  margin: 0px;
  line-height: 22px;
  letter-spacing: .15px;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
  font-family: "Roboto", sans-serif;
}

.home-twosubscribe__title {
  width: 100%;
  margin-bottom: 35px;
  text-align: center;
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  font-weight: bold;
  letter-spacing: .9px;
}

.home-twosubscribe__box .home-subscribe__item-right {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 90px 0px;
  background: url(../img/home-twosubscibe--twobackground.png);
  background-size: cover;
  position: relative;
}

.home-twosubscribe__box .home-subscribe__item-right::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.1);
}

.home-twosubscribe-find__box {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 50px;
}

.home-twosubscribe-find__box input[type='text'] {
  width: 100%;
  height: 37px;
  padding: 13px 17px;
  border: solid 1px #000;
}

.home-twosubscribe-find__box input[type='text']::placeholder {
  color: #000;
}

.home-twosubscribe-find__box input[type="submit"] {
  position: absolute;
  top: 0;
  right: 0;
  width: 80px;
  height: 100%;
  font-size: 16px;
  font-family: "Staatliches", cursive;
  letter-spacing: 1px;
  color: #fff;
  border: 0px;
  background: #000;
}

.home-twosubscribe__around {
  position: relative;
  width: 500px;
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: #000;
  border-radius: 50%;
}

.home-twosubscribe__around svg {
  margin-bottom: 35px;
}

.home-twosubscribe__around-title {
  margin: 0px;
  margin-bottom: 15px;
  color: #fff;
  letter-spacing: 3px;
  font-family: "Staatliches", cursive;
  font-weight: 300;
  font-size: 49px;
}

.home-twosubscribe__around-subtitle {
  margin: 0px;
  margin-bottom: 50px;
  color: #fff;
  letter-spacing: .3px;
  font-weight: 300;
  font-family: "Staatliches", cursive;
  font-size: 19px;
}

.home-twosubscribe__around-link {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 28px;
  color: #fff;
  text-decoration: none;
  font-weight: 300;
  font-family: "Staatliches", cursive;
  font-size: 19px;
  border: solid 1px #fff;
}